header {
    position: relative; }

nav {
    height: 50px;
    background-color: #212121; }

.hamburger {
    display: none; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    .menu_link {
        color: #ffffff;
        font-size: 15px;
        font-weight: 300;
        text-align: left;
        text-decoration: none; }
    .menu_item {
        border-right: 2px dotted black;
        padding-right: 40px;
        &:last-child {
            border-right: none; } } }


.subheader {
    position: absolute;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    &_logo {
        display: block;
        margin-top: 16px;
        max-width: 100%; }
    &_official {
        opacity: 0.5;
        color: #ffffff;
        font-size: 13px;
        font-weight: 400;
        text-align: left; }
    &_call {
        margin-top: 18px;
        color: #ffffff;
        font-size: 12px;
        font-weight: 300;
        text-align: right; }
    &_phone {
        display: block;
        color: #ffffff;
        font-size: 22px;
        font-weight: 400;
        text-align: right;
        text-transform: uppercase;
        text-decoration: none; }



    &_btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.12);
        border: none;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        border-radius: 5px;
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;
        @include btn_hover; } }


.promo {
    min-height: 799px;
