.mobil {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../maket/Teil-3/background.png') center center/cover no-repeat;
    &_block {
        margin: 0 auto;
        padding: 5px 12px;
        width: 112px;
        height: 24px;
        background-color: #000000;
        &_text {
            color: #ffffff;
            font-size: 11px;
            font-weight: 300;
            text-align: center;
            text-transform: uppercase; } }
    &_header {
        margin-top: 38px;
        color: #222222;
        font-size: 38px;
        font-weight: 700;
        text-align: center;
        text-transform: uppercase; }
    h3 {
        margin-top: 19px;
        color: #222222;
        font-size: 17px;
        font-weight: 400;
        text-align: center; }
    .row {
        margin-top: 32px; }
    &_pack {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        &_1 {
            background: url('../maket/Teil-3/img_1.png') center center/cover no-repeat; }
        &_2 {
            background: url('../maket/Teil-3/img_2.png') center center/cover no-repeat; }
        &_3 {
            background: url('../maket/Teil-3/img_3.png') center center/cover no-repeat; }
        &_4 {
            background: url('../maket/Teil-3/img_4.png') center center/cover no-repeat; }
        &_5 {
            background: url('../maket/Teil-3/img_5.png') center center/cover no-repeat; }
        &_6 {
            background: url('../maket/Teil-3/img_6.png') center center/cover no-repeat; }
        &_text {
            padding-top: 92px;
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase; }
        &_round {
            position: absolute;
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            padding: 6px;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            cursor: pointer;
            &:before {
                content: '';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $main-color; }
            &:after {
                content: '';
                position: absolute;
                display: block;
                width: 2px;
                height: 14px;
                background-color: $main-color;
                left: 50%;
                transform: translateX(-50%); }
            &:hover {
                &:before, &:after {
                    background-color: $second-color;
                    transition: 0.5s all; } } } } }

