@media (max-width: 1200px) {
    nav {
        .menu {
            &_item {
                padding-right: 20px; }
            &_link {
                font-size: 13px; } } }

    .promo {
        &_header {
            font-size: 50px; } }
    .reason {
        &_pack {
            align-items: flex-start; }
        &_block {
            &_text {
                width: 300px; } } }
    .bottom {
        width: 100%; } }

@media (max-width: 991px) {
    nav {
        .menu {
            &_item {
                padding-right: 8px; }
            &_link {
                font-size: 10px; } } }

    .subheader {
        &_logo {
            img {
                width: 100%; } }
        &_official {
            font-size: 11px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            width: 100%;
            height: 36px;
            margin-top: 22px; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; } }
    .reason {
        &_pack {
            flex-direction: column;
            align-items: center; }
        &_img {
            margin-right: 0; }
        &_block {
            &_text {
                width: 300px;
                margin-top: 20px; } } }
    .mobil {
        &_block {
            width: 100%; } }
    .uber {
        &_block {
            width: 100%; } }
    .bottom {
        &_linkbox2 {
            left: 300px; } } }



@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8);
        transition: 0.6s all;
        &_active {}

        &_item {
            padding-right: 0;
            border: none; }
        &_link {
            font-size: 20px; } }

    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50px;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block;
        margin-top: 10px;
        font-size: 16px;
        font-weight: 400;
        text-transform: uppercase;
        color: #ffffff;
        text-align: center; }

    .subheader {
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 10px; } }

    .promo {
        &_header {
            font-size: 40px;
            line-height: 45px; } } }

@media (max-width:575px) {
    nav {
        height: 30px; }
    .header {
        height: 30px; }
    .subheader {
        height: 60px;
        top: 30px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 16px; }
        &_phone {
            font-size: 12px;
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 11px; }
        &_btn {
            width: 116px;
            height: 28px;
            font-size: 10px;
            margin-top: 6px; } }

    .promo {
        min-height: 400px;
        padding: 73px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        &_subheader {
            font-size: 20px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30; }
        &_btn {
            width: 216px;
            height: 56px;
            margin-top: 72px; } }
    .menu {
            width: 85%; }
    .reason {
        padding: 22px 0 36px 0;
        &_pack {
            margin-top: 26px;
            width: 100%;
            margin-top: 27px; }
        &_img {
            width: 100px;
            height: 100px; }
        &_header {
            font-size: 16px;
            margin-top: 15px;
            line-height: 16px; }
        &_text {
            font-size: 16px; } } }























